<template>
    <div v-if="book">
        <b-card :title="book.title"
                :img-src="book.thumbnailUrl"
                img-alt="Image"
                img-top
                tag="article"
                style="max-width: 30rem;"
                class="mb-2">
            <b-card-text>
                {{ book.descr }}
            </b-card-text>

            <b-button to="/" variant="primary">Back</b-button>
        </b-card>
    
        <br/>
        <br/>
        <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-group id="input-group-1"
                          label="Short Description:"
                          label-for="input-1"
                          description="Book short description">
                <b-form-textarea id="input-1"
                              v-model=  "book.descr"
                               rows="7"
                              placeholder="Enter Short Description"></b-form-textarea>
            </b-form-group>
            <b-button type="submit" variant="primary">Submit</b-button>
            &nbsp; &nbsp;
            <b-button type="reset" variant="danger">Reset</b-button>
        </b-form>

    </div>
</template>

<script>
    import axios from 'axios';

    export default {
        name: 'Book',
        props: ["id"],
        data: () => ({
            book: null,
            show: true
        }),
        mounted() {
            axios.get(`https://localhost:5001/books/${this.id}`)
                .then(response => {
                    this.book = response.data;
                    this.Console.log(this.book);
                });
        },
        methods
            : {
            onSubmit(evt) {
                evt.preventDefault()
               // alert(JSON.stringify(this.form))

                     axios.post("https://localhost:5001/books/BookUpsert"
                    , {
                        'book': this.book,
                    }
                )
                    .then(res => {
                        this.book = res.data.results
                        return res.data.results;
        })
       
            },
            onReset(evt) {
                evt.preventDefault()
                // Reset our form values
                this.book.descr = ''

                // Trick to reset/clear native browser form validation state
                this.show = false
                this.$nextTick(() => {
                    this.show = true
                })
            }
        }
    }
</script>